import styles from "./QuestElement.module.css";
import { useState } from "react";
export function QuestElement({ number, title, description }) {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <div
      className={styles["container"]}
      onClick={() => (isOpen ? setIsOpen(false) : setIsOpen(true))}
    >
      <div className={`${styles.title} ${isOpen ? styles.isopen : ""}`}>
        <span>{number}.</span>
        <span>{title} </span>
        <img className={styles.icon} src="/icons/plus.svg" alt="" />
      </div>
      <div
        className={`${styles.description} ${isOpen ? styles.descriptionOpen : ""}`}
      >
        <p className={styles.descriptionText}>{description}</p>
      </div>
    </div>
  );
}
