import styles from "./WhoWant.module.css";
import WhoWantCard from "./WhoWantCard";
import React, { useLayoutEffect, useRef } from "react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
gsap.registerPlugin(ScrollTrigger);
ScrollTrigger.config({
  ignoreMobileResize: true,
});
export function WhoWant() {
  const viewportRef = useRef(null);
  const headingRef = useRef(null);
  const cardsContainerRef = useRef(null);
  const g1Ref = useRef(null);
  const g2Ref = useRef(null);
  const g3Ref = useRef(null);

  useLayoutEffect(() => {
    ScrollTrigger.matchMedia({
      "(min-width: 769px)": function () {
        const ctx = gsap.context(() => {
          const section = viewportRef.current;
          const container = cardsContainerRef.current;
          const heading = headingRef.current;
          const cards = [
            container.querySelector(`.${styles.card1}`),
            container.querySelector(`.${styles.card2}`),
            container.querySelector(`.${styles.card3}`),
            container.querySelector(`.${styles.card4}`),
            container.querySelector(`.${styles.card5}`),
            container.querySelector(`.${styles.card6}`),
            container.querySelector(`.${styles.card7}`),
          ].filter(Boolean);

          const tl_desktop = gsap.timeline({
            defaults: { ease: "none" },
            scrollTrigger: {
              trigger: section,
              start: "center center",
              end: "+=800vh",
              scrub: 0.1,
              pin: true,
            },
          });

          tl_desktop.to({}, { duration: 0.5 });

          tl_desktop.fromTo(
            heading,
            { y: "0", opacity: 1 },
            {
              y: 0,
              opacity: 1,
              duration: 40,
              ease: "none",
            }
          );

          const overlap = 1;
          tl_desktop.fromTo(
            cards,
            { y: "110vh", opacity: 0 },
            {
              y: 0,
              opacity: 1,
              duration: 60,
              ease: "none",
              stagger: 10,
            },
            `-=${overlap}`
          );
        });
        return () => ctx.revert();
      },
      "(max-width: 768px)": function () {
        const ctx = gsap.context(() => {
          const section = viewportRef.current;
          const container = cardsContainerRef.current;
          const heading = headingRef.current;
          const cards = [
            container.querySelector(`.${styles.card1}`),
            container.querySelector(`.${styles.card2}`),
            container.querySelector(`.${styles.card4}`),
            container.querySelector(`.${styles.card6}`),
            container.querySelector(`.${styles.card3}`),
            container.querySelector(`.${styles.card5}`),
            container.querySelector(`.${styles.card7}`),
          ].filter(Boolean);

          const tl_mobile = gsap.timeline({
            defaults: { ease: "none", autoRound: true },
            scrollTrigger: {
              trigger: section,
              start: "top top",
              end: "+=1200vh",
              scrub: 0.8,
              pin: true,
              invalidateOnRefresh: true,
              fastScrollEnd: true,
            },
          });

          tl_mobile.fromTo(
            heading,
            { y: "0" },
            {
              y: 0,
              duration: 35,
              ease: "none",
            }
          );

          const overlap = 1;
          tl_mobile.fromTo(
            cards,
            { y: "110vh", opacity: "0" },
            {
              y: 0,
              duration: 35,
              ease: "none",
              stagger: 0.5,
              opacity: 1,
            },
            `-=${overlap}`
          );
        });
        return () => ctx.revert();
      },
    });
  }, []);

  return (
    <div ref={viewportRef} className={styles.animationViewport}>
      <div ref={cardsContainerRef} className={styles["container__content"]}>
        <div className={styles.group1} ref={g1Ref}>
          <WhoWantCard
            className={styles.card1}
            text={`Улучшить качество и тонус
    тела без изнурительных
    тренировок и диет`}
            number="1"
          />
          <WhoWantCard
            className={styles.card2}
            text={`Подтянуть живот
    и убрать бока`}
            number="2"
          />
          <WhoWantCard
            className={styles.card3}
            text="Снизить лишний вес"
            number="5"
          />
        </div>
        <div className={styles.group2} ref={g2Ref}>
          <WhoWantCard
            className={styles.card4}
            text="Убрать рыхлость и дряблость"
            number="3"
          />
          <h2 ref={headingRef} className={styles["text"]}>
            ПРОГРАММЫ ДЛЯ ТЕХ, <br />
            <span>КТО ХОЧЕТ</span>
          </h2>
          <WhoWantCard
            className={styles.card5}
            text={`Улучшить качество кожи,
    сгладить целлюлит`}
            number="6"
          />
        </div>
        <div className={styles.group3} ref={g3Ref}>
          <WhoWantCard
            className={styles.card6}
            text={`Придать телу рельефность`}
            number="4"
          />
          <WhoWantCard
            className={styles.card7}
            text={`Убрать лишние
    жировые отложения`}
            number="7"
          />
        </div>
      </div>
    </div>
  );
}
